:root {
    --font-main: 'Montserrat', sans-serif;

    --color-accent: #fb6d3a;
    --color-body: #f5f7fa;
    --color-shadow: rgba(0, 0, 0, 0.1);
    --color-title: #000000;
    --color-backgroud: #ffffff;
    --color-text: #595959;
    --color-linck: #565e6a;
    --color-background-sidebar: #1e2939;
    --color-work-experience: #a8a8a8;
    --color-hover: #1976d2;
}

/* my progects sectio */

.myProjectsSection {
    margin: 0 0 40px 0;
    /* padding-right: 50px; */
}

.myProjectsText {
    display: flex;
    justify-content: space-around;
    font-style: normal;
    font-weight: normal;
    font-size: 14px;
    line-height: 24px;
    color: var(--color-text);
    margin-left: 5px;
}

.myProjectsList {
    padding: 0 0 0 17px;
    margin: 0;
}

.projectsItem {
    color: var(--color-title);
    font-weight: bold;
}

.myProjectsLink {
    flex-shrink: 0;
    font-style: normal;
    font-weight: normal;
    font-size: 14px;
    line-height: 24px;
    color: var(--color-text);
    text-decoration: none;
}
.myProjectsLink:hover, .myProjectsLink:focus{
    color: var(--color-hover);
}

.myProjectsBracket {
    font-style: normal;
    font-weight: bold;
    font-size: 14px;
    line-height: 24px;
    color: var(--color-title);
}

.myProjectsBracketLine {
    width: 100%;
    height: 18px;
    border-bottom: 1px dashed var(--color-text);
    margin-right: 5px;
    margin-left: 5px;
}

.myProjectsTechnology {
    flex-shrink: 0;
}

.mainContentTitle {
    display: flex;
    align-items: flex-start;
    font-style: normal;
    font-weight: bold;
    font-size: 22px;
    line-height: 27px;
    color: var(--color-title);
    margin-bottom: 10px;
    margin-top: 0;
}

.mainContentTitle svg {
    margin-right: 10px;
}



/* !!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!! */




@media print {

    .myProjectsSection {
            margin: 0 0 20px 0;
            padding-right: 20px;
        }

.myProjectsLink,
    .projectsItem,
    .myProjectsTechnology,
    .myProjectsBracket {

        line-height: 1.25;

    }
}